body{
    background-color: white;
    margin: 6vw;
    overflow: hidden;
}
.statement{
    color: black;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 3vmax;
    line-height: 6vmin;
    position: relative;
    width:100vw;
    height: 100vh; 
}
.statement:hover .en{
    -webkit-transition: 50s linear;
    -o-transition: 50s linear;
    transition: 50s linear;
    top: 200%;
    right:-10%
}
.statement:hover .fr{
    -webkit-transition: 50s linear;
    -o-transition: 50s linear;
    transition: 50s linear;
    top: -200%;
    right:-10%;
}
.en {
    position: absolute;
    top:-4vh;
    right:0vw;
    width: 100%;
    height: 50vh;
    -webkit-transition: 10s linear;
    -o-transition: 10s linear;
    transition: 10s linear;
}
.fr {
    position: absolute;
    top:20vh;
    right:0vw;
    width: 100%;
    height: 50vh;
    -webkit-transition: 10s linear;
    -o-transition: 10s linear;
    transition: 10s linear;
}
.en:hover .en{
        -webkit-transition: 30s linear;
    -o-transition: 30s linear;
    transition: 30s linear;
    top: -200%;
    right:10%;
}
.fr:hover .fr{
        -webkit-transition: 30s linear;
    -o-transition: 30s linear;
    transition: 30s linear;
    top: 200%;
    right:20%;
}




